"use client";
import { useParams } from "next/navigation";
import React, { useState } from "react";

/* ================= TYPES ================= */

type Role = "Caller" | "Agent";

interface Comment {
    id: number;
    author: string;
    role: Role;
    message: string;
    time: string;
}

interface TimelineItem {
    status: string;
    user: string;
    time: string;
}

interface Ticket {
    id: number;
    summary: string;
    status: string;
    priority: string;
    assignmentGroup: string;
    ci: string;
    timeline: TimelineItem[];
    comments: Comment[];
}

/* ================= PAGE ================= */

export default function TicketDetailPage() {
    const { id } = useParams();
    const [ticket, setTicket] = useState<Ticket>(mockTicket(Number(id)));
    const [comment, setComment] = useState("");

    const currentUser = {
        name: "Current User",
        role: "Caller" as Role, // change to "Agent" for agent view
    };

    function addComment() {
        if (!comment.trim()) return;

        const newComment: Comment = {
            id: Date.now(),
            author: currentUser.name,
            role: currentUser.role,
            message: comment,
            time: new Date().toLocaleString(),
        };

        setTicket({
            ...ticket,
            comments: [...ticket.comments, newComment],
        });

        setComment("");
    }

    return (
        <div className="p-6 bg-gray-100 min-h-screen space-y-6">

            {/* ===== TICKET HEADER ===== */}
            <div className="bg-white rounded shadow p-6">
                <h1 className="text-xl font-semibold">{ticket.summary}</h1>

                <div className="grid grid-cols-4 gap-4 mt-4 text-sm">
                    <div><b>Status:</b> {ticket.status}</div>
                    <div><b>Priority:</b> {ticket.priority}</div>
                    <div><b>Assignment:</b> {ticket.assignmentGroup}</div>
                    <div><b>CI:</b> {ticket.ci}</div>
                </div>
            </div>

            {/* ===== ACTIVITY TIMELINE ===== */}
            <div className="bg-white rounded shadow p-6">
                <h2 className="font-semibold mb-4">Ticket Activity</h2>
                <div className="border-l-2 pl-4 space-y-4">
                    {ticket.timeline.map((t, i) => (
                        <div key={i}>
                            <div className="font-medium">{t.status}</div>
                            <div className="text-xs text-gray-500">
                                {t.user} Â· {t.time}
                            </div>
                        </div>
                    ))}
                </div>
            </div>

            {/* ===== COMMENTS SECTION ===== */}
            <div className="bg-white rounded shadow p-6">
                <h2 className="font-semibold mb-4">Comments</h2>

                {/* COMMENTS LIST */}
                <div className="space-y-4 mb-4">
                    {ticket.comments.map((c) => (
                        <div
                            key={c.id}
                            className={`p-4 rounded border ${c.role === "Agent"
                                    ? "bg-blue-50 border-blue-200"
                                    : "bg-gray-50 border-gray-200"
                                }`}
                        >
                            <div className="flex justify-between items-center mb-1">
                                <div className="font-medium">
                                    {c.author}
                                    <span className="ml-2 text-xs px-2 py-0.5 rounded bg-gray-200">
                                        {c.role}
                                    </span>
                                </div>
                                <div className="text-xs text-gray-500">{c.time}</div>
                            </div>
                            <div className="text-sm">{c.message}</div>
                        </div>
                    ))}
                </div>

                {/* ADD COMMENT */}
                <div className="border-t pt-4">
                    <textarea
                        className="w-full border rounded px-3 py-2 mb-3"
                        rows={3}
                        placeholder="Add a comment..."
                        value={comment}
                        onChange={(e) => setComment(e.target.value)}
                    />

                    <div className="flex justify-end">
                        <button
                            onClick={addComment}
                            className="bg-blue-600 text-white px-4 py-2 rounded"
                        >
                            Add Comment
                        </button>
                    </div>
                </div>
            </div>

        </div>
    );
}

/* ================= MOCK DATA ================= */

function mockTicket(id: number): Ticket {
    return {
        id,
        summary: "VPN not working",
        status: "In Progress",
        priority: "High",
        assignmentGroup: "IT Support",
        ci: "VPN",
        timeline: [
            { status: "Submitted", user: "Caller", time: "Jan 2, 10:00" },
            { status: "In Progress", user: "John", time: "Jan 2, 11:00" },
        ],
        comments: [
            {
                id: 1,
                author: "Caller",
                role: "Caller",
                message: "VPN disconnects frequently.",
                time: "Jan 2, 10:05",
            },
            {
                id: 2,
                author: "John",
                role: "Agent",
                message: "Investigating the VPN logs.",
                time: "Jan 2, 11:10",
            },
        ],
    };
}
